@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.2/css/bulma.min.css);
@import url(https://fonts.googleapis.com/css2?family=Odibee+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.cronometer-header {
  padding: 20px;
  background-color: #3E2A35;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.cronometer-page {
  margin: 30px 0;
  text-align: center;
}

.cronometer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.time-controller {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.time-controller input {
  font-family: 'Odibee Sans', cursive;
  text-align: center;
  font-size: 80px;
  margin: 0;
  width: 100px;
  height: 100px;
}

.double-dots {
  font-size: 80px;
  font-weight: 700;
  text-align: center;
  padding-bottom: 14px;
}

.time-starter {
  margin-top: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.predefined-times {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0 30px;
}

.iframeSec {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}

.iframeSec h3 {
  text-align: center;
}

.pt {
  width:40%
}

.playlist-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 30px;
}

.playlist-turmas {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.footer {
  background-color: #3E2A35;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-height: 30px;
  -webkit-align-items: center;
          align-items: center;
}

.credits {
  width: 50%;
  color: white;
  font-family: 'Staatliches', cursive;
  font-weight: 400;
  font-size: 25px;
}

.network {
  display: -webkit-flex;
  display: flex;
  font-size: 50px;
}

.network a {
  color: white;
  margin: 0 10px;
}

.network a:hover {
  color: #525252;
}

.credits, .network {
  padding-top: 40px;
}

.darkmode {
  border: none;
  background-color: transparent;
  font-size: 40px;
  color: white;
}

.darkmode:hover {
  color: #525252;
}
